import { AccountInfo, AuthenticationResult, PublicClientApplication } from "@azure/msal-browser";
import { msalConfig, b2cPolicies, apiConfig } from "./msalConfig";

declare var basePath: string;
declare var programPath: string;
declare var includeJS: (path: string) => void;

function signOut() {
    // request: https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-browser/docs/request-response-object.md#request
    //TODO: redirect uri neresi ?
    msalObj.logoutRedirect({ postLogoutRedirectUri: msalConfig.auth.redirectUri });
}
function signIn() {
    msalObj.loginRedirect({ scopes: ["openid", ...apiConfig.b2cScopes] });
}

function setAccount(account: AccountInfo) {
    //TODO: implement this :)
    alert("hello: " + JSON.stringify(account));
    includeJS(programPath);
}

function selectAccount() {
    const currentAccounts = msalObj.getAllAccounts(); //account retrieval https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-common/docs/Accounts.md
    if (currentAccounts.length < 1) { return false; }
    if (currentAccounts.length === 1) {
        setAccount(currentAccounts[0]);
        return true;
    }

    //TODO: understand this
    /**
     * Due to the way MSAL caches account objects, the auth response from initiating a user-flow
     * is cached as a new account, which results in more than one account in the cache. Here we make
     * sure we are selecting the account with homeAccountId that contains the sign-up/sign-in user-flow, 
     * as this is the default flow the user initially signed-in with.
     */
    const accounts = currentAccounts.filter(account =>
        account.homeAccountId.toUpperCase().includes(b2cPolicies.names.signUpSignIn.toUpperCase()) &&
        account.idTokenClaims!.iss!.toUpperCase().includes(b2cPolicies.authorityDomain.toUpperCase()) &&
        account.idTokenClaims!.aud === msalConfig.auth.clientId);

    if (accounts.length > 1) {
        // localAccountId identifies the entity for which the token asserts information.
        if (accounts.every(account => account.localAccountId === accounts[0].localAccountId)) {
            // All accounts belong to the same user
            setAccount(accounts[0]);
            return true;
        } else {
            // Multiple users detected. Logout all to be safe.
            signOut();
            return undefined;
        };
    } else if (accounts.length === 1) {
        setAccount(accounts[0]);
        return true;
    }
    return false;
}

async function handleResponse(response: AuthenticationResult) {
    // response object: https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-browser/docs/request-response-object.md#response
    if (response.account !== null) {
        setAccount(response.account);
    } else {
        selectAccount();
    }
}

async function handleRedirect() {
    const response = await msalObj.handleRedirectPromise();
    if (!response) { return false; }
    /**
     * For the purpose of setting an active account for UI update, we want to consider only the auth response resulting
     * from SUSI flow. "tfp" claim in the id token tells us the policy (NOTE: legacy policies may use "acr" instead of "tfp").
     * To learn more about B2C tokens, visit https://docs.microsoft.com/en-us/azure/active-directory-b2c/tokens-overview
    */
    if ((response.idTokenClaims as { tfp: string })['tfp'].toUpperCase() === b2cPolicies.names.signUpSignIn.toUpperCase()) {
        handleResponse(response);
    }
    return true;
}








// Create the main myMSALObj instance
const msalObj = new PublicClientApplication(msalConfig);
(async () => {
    const isRefreshSelected = selectAccount();//page refresh
    const isRedirect = await handleRedirect();

    if (!isRedirect && !isRefreshSelected) {
        signIn();
    }
})();